.clusterConecel {
	background-clip: padding-box;
	background-color: rgba(218, 71, 76, 0.6);
	border-radius: 20px;
}

.clusterConecel div {
	width: 30px;
	height: 30px;
	margin-left: 5px;
	margin-top: 5px;
	text-align: center;
	border-radius: 15px;
	font: 12px "Helvetica Neue", Arial, Helvetica, sans-serif;
	background-color: rgba(218, 38, 44, 0.6);
}

.clusterConecel span {
	line-height: 30px;
}

