.leaflet-touch .leaflet-sidebar>.leaflet-control {
    box-shadow: none;
    border: 2px solid rgba(0, 0, 0, 0.2);
    background-clip: padding-box;
}

@media (max-width: 767px) {
    .leaflet-sidebar {
        width: 100%;
        padding: 0;
    }
    .leaflet-sidebar.left.visible~.leaflet-left {
        left: 100%;
    }
    .leaflet-sidebar.right.visible~.leaflet-right {
        right: 100%;
    }
    .leaflet-sidebar.left {
        left: -100%;
    }
    .leaflet-sidebar.left.visible {
        left: 0;
    }
    .leaflet-sidebar.right {
        right: -100%;
    }
    .leaflet-sidebar.right.visible {
        right: 0;
    }
    .leaflet-sidebar>.leaflet-control {
        box-shadow: none;
        border-radius: 0;
    }
    .leaflet-touch .leaflet-sidebar>.leaflet-control {
        border: 0;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .leaflet-sidebar {
        width: 305px;
    }
    .leaflet-sidebar.left.visible~.leaflet-left {
        left: 305px;
    }
    .leaflet-sidebar.right.visible~.leaflet-right {
        right: 305px;
    }
}

@media (min-width: 992px) and (max-width: 1199px) {
    .leaflet-sidebar {
        width: 390px;
    }
    .leaflet-sidebar.left.visible~.leaflet-left {
        left: 390px;
    }
    .leaflet-sidebar.right.visible~.leaflet-right {
        right: 390px;
    }
}

@media (min-width: 1200px) {
    .leaflet-sidebar {
        width: 460px;
    }
    .leaflet-sidebar.left.visible~.leaflet-left {
        left: 460px;
    }
    .leaflet-sidebar.right.visible~.leaflet-right {
        right: 460px;
    }
}

.leaflet-sidebar .close {
    position: absolute;
    right: 40px;
    top: 20px;
    width: 31px;
    height: 31px;
    color: whitesmoke;
    font-size: 25px;
    line-height: 1em;
    text-align: center;
    /* background: rgba(0, 0, 0, 0.005); */
    border-radius: 6px;
    cursor: pointer;
    z-index: 1000;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.leaflet-sidebar .menu {
    position: absolute;
    right: 40px;
    top: 100px;
    width: 100%;
    height: 70px;
    color: #e7040f;
    font-size: 25px;
    line-height: 1em;
    text-align: center;
    background: rgba(0, 0, 0, 0.005);
    border-radius: 6px;
    cursor: pointer;
    z-index: 1000;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.leaflet-sidebar .close:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(255, 255, 255, 0.5)
}

