.profile-modal {
	background-color: rgba(255, 255, 255, 0.8);
	position: fixed;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: center;
}

.tc {
	padding: 0;
	margin: 0;
}
.avatar {
	/* height: 100%;
    max-height: 100%; */
	padding: 0;
	margin: 0;
	/* position: relative; */
	cursor: pointer;
}

.dropDownMenu {
	top: 10px !important;
	box-sizing: border-box;
	/* min-width: 100%; */
	/* z-index: 20000!important; */
}
.dropdown-item {
	/* min-width: 100%; */
	box-sizing: border-box;
	background-color: Transparent;
	background-repeat: no-repeat;
	border: none;
	margin-bottom: 0;
	padding-bottom: 0;
	color: #222222;
	/* cursor:pointer; */
	overflow: hidden;
	outline: none;
}
.dropdown-item:hover {
	text-decoration: underline;
}
.modal-close {
	font-size: 3rem;
	font-style: bold;
	cursor: pointer;
}

.modal-close:hover {
	color: grey;
	cursor: pointer;
}
